:root {
    --background_color: #00000071 !important;
    --info-text: #98a8ac;
    --button-primary-color:  rgb(5, 112, 199);
    --button-positive-color:  rgba(33, 146, 35, 0.716);
    --primary-color: #0798bc;
    --positive-color: #0b9614;
    --negative-color: #af1919;
    --warn-color: #9a7f12;
    --bacground-filter: #003cc74b;
    --bacground-filter-2: #272727a5;
    --bacground-filter-3: #89e58e7e;
    --bacground-filter-4: #af3b3b7e;
}

.image {
    width: 90vw;
    margin: 10px auto;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(39, 39, 39, 0.6);
}

.main {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 50px 10px;
    font-weight: 300;
    text-align: center;
    align-items: center;
    justify-content: center;

}

.main > span{
    font-weight: 400;
    color: var(--button-primary-color);
}

.downloand {
    margin: 0 auto;
    text-align: center;
    font-weight: 200;
    cursor: pointer;
}

.downloand:active {
    opacity: 0.8;
}

.warn {
    padding: 10px;
    color: var(--warn-color);
    text-align: center;
}

.success {
    padding: 10px;
    text-align: center;
}

.link {
    margin: 20px;
    text-align: center;
}

.downloand-error {
    text-align: center;
    text-transform: uppercase;
}